@import '../../base';

%standard-font {
  font-family: $font-open-sans;
  font-weight: normal;
  font-style: normal;
  font-stretch: normal;
  line-height: normal;
  letter-spacing: -0.1px;
  text-align: center;
  color: white;
}
$heavy-weight: 700;
$horizontal-anim-duration: 90s;
$vertical-anim-duration: 4.5s;

.press {
  position: relative;
  overflow: hidden;
  margin-top: 60px;

  .title-container {
    max-width: 1000px;
    text-align: center;
    margin: auto;
    padding: 20px;

    @media screen and(min-width: 820px){
      padding: 40px;
    }

    .title {
      @extend %standard-font;
      display: inline;
      font-size: $font-size-big;
      font-weight: $heavy-weight;
      color: $color-title !important;

      @media screen and(min-width: 820px){
        font-size: $font-size-extra-big;
      }
    }

    .desc {
      text-align: center;
      @extend %standard-font;
      font-size: $font-size-small;
      font-weight: $font-weight-normal;
      color: $color-title !important;

      @media screen and (min-width: 1440px) {
        font-size: 19.5;
      }
    }
  }
  .content {

    .content-row {
      display: flex;
      flex-wrap: wrap;
      margin-left: auto;
      margin-right: auto;
      max-width: 640px;
        
      @media screen and(min-width: 1200px){
        max-width: 700px;
      }

      @media screen and(min-width: 1440px){
        max-width: 1050px;
      }

      .press-box  {
        text-decoration: none;
        background-image: linear-gradient(108deg, #F7F7F7, #F7F7F7);

        &:before {
          background-image: linear-gradient(108deg, #E6E9EC, #E6E9EC);
        }

        flex: 0 0 auto;
        width: 200px;
        height: 350px;
        margin: auto auto 24px auto;
        padding: 20px;
    
        @media screen and(min-width: 500px){
          width: 240px;
          margin: auto auto 24px auto;
        }
        
        @media screen and(min-width: 820px){
          width: 300px;
          margin: auto auto 24px auto;
        }

        @media screen and(min-width: 1200px){
          width: 300px;
          margin: 12px 24px;
        }

        .date {
          @extend %standard-font;
          font-size: $font-size-tiny;
          color: $color-body;
          text-align: left !important;
          padding: 10px 0;
          margin: auto;
          line-height: 24px;
          height: 70px;
      
          @media screen and (min-width: 1440px) {
            font-size: $font-size-small;
          }

          img {
            max-height: 40px;
            max-width: 120px;
            float: right;
            opacity: 0.4;
          }
        }

        .title {
          @extend %standard-font;
          font-size: $font-size-tiny;
          font-weight: $font-weight-bold;
          color: $color-title;
          text-align: left !important;
          margin: auto;
      
          @media screen and(min-width: 820px){
            font-size: $font-size-regular;
          }
        }
      
        .desc {
          @extend %standard-font;
          font-size: $font-size-tiny;
          color: $color-body;
          text-align: left !important;
          padding: 10px 0;
          margin: auto;
          line-height: 24px;
      
          @media screen and (min-width: 1440px) {
            font-size: $font-size-small;
          }
        }
      }
    }
  }
}